@font-face {
    font-family: 'montserrat';
    src:url('https://fonts.googleapis.com/css?family=Montserrat');

  }
@font-face{
    font-family:'opensans';
    src:url('https://fonts.googleapis.com/css?family=Montserrat|Open+Sans');
}
.register_form{
    display:flex;
    flex-direction: column;
    height:60%;
    width:60%;
    margin: 0 auto;
    justify-content: space-between;
    margin-top:20%;
}
label, legend{
    margin: 0 auto;
    color:#FFFFFF;
    
}
label{
    font-family: 'opensans';
    font-size:1.2em;

}
legend{
    font-family: 'montserrat';
    font-size:1.5em;
    width:80%;
}
.wrapper{
    width:50%;
    margin: 0 auto;
    height:100%;
}
#root{
    height:100%;
    width:100%;
}
html, body{
    height:100%;
    width:100%;
    background-color: #09131E;
}
.information_field{
    width:60%;
    padding:10px;
    font-size: 1.2em;
    margin: 0 auto;
    border-radius:4px;
}
.button{
    width:80%;
    padding:20px;
    background-color:#DAE14A;
    font-size:1.2em;
    color:white;
    margin:0 auto;
}
.button:disabled,
.button[disabled]{
  opacity:0.7;
}